@use '../utilities/breakpoints.scss' as *;

.container {
  width: min(90rem, 100%);
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;

  @include breakpoint(medium) {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}

.--margin-top {
  margin-top: 2rem;

  @include breakpoint(medium) {
    margin-top: 3.5;
  }
}