html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: var(--default-font);
  overflow-x: hidden;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

button {
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
}

button:hover {
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
}

button:active {
  outline: none;
  border: none;
}

button:focus {
  outline: 0;
}

h1, h2, h3, h4 {
  margin-top: 0;
  font-family: var(--font-heading);
  font-weight: 900;
  color: var(--dark-desaturated-blue);
}

h1 {
  font-size: clamp(36px, 5vw, 44px);
  letter-spacing: 5px;
}

h2 {
  font-size: clamp(30px, 5vw, 34px);
}

h3 {
  font-size: clamp(24px, 3vw, 28px);
}

h4 {
  font-size: clamp(16px, 2vw, 24px);
}

a, a:visited, a:active {
  text-decoration: none;
}

.learn-btn {
  font-size: clamp(14px, 3vw, 18px);
  font-weight: 900;
  font-family: var(--font-heading);
  color: var(--dark-desaturated-blue);
  position: relative;
}

.learn-btn::after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  height: 6px;
  width: 100%;
  border-radius: 20px;
  z-index: -1;
  opacity: 0.5;
}

.learn-btn:hover::after {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

:root {
  --font-heading: "Fraunces", serif;
  --font-body: "Barlow", sans-serif;
  --default-font: 18px;
}

:root {
  --light-blue: hsl(200, 100%, 62%);
  --light-green: hsl(167, 44%, 70%);
  --soft-red: hsl(7, 99%, 70%);
  --yellow: hsl(51, 100%, 49%);
  --dark-desaturated-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --dark-moderate-cyan: hsl(168, 34%, 41%);
  --dark-desaturated-blue: hsl(212, 27%, 19%);
  --dark-grayish-blue: hsl(213, 9%, 39%);
  --grayish-blue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
  --light-blue-hover: hsla(200, 100%, 78%, 0.877);
}

