.footer__wrapper {
  padding: 3.5rem 0;
  background-color: var(--light-green);
}
.footer__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer__content-logo {
  margin-bottom: 2.5rem;
}
.footer__content-logo > img {
  width: 200px;
}
.footer__content-nav {
  margin-bottom: 2rem;
}
.footer__content-nav-menu {
  width: 280px;
}
.footer__content-nav-menu > ul {
  display: flex;
  justify-content: space-between;
}
.footer__content-nav-menu > ul > li > a {
  color: var(--dark-moderate-cyan);
}
.footer__content-nav-menu > ul > li > a:hover {
  color: var(--white);
  transition: all 0.3s ease-in-out;
}
.footer__content-social {
  margin: 3rem 0 2rem 0;
  width: 150px;
}
.footer__content-social > ul {
  display: flex;
  justify-content: space-between;
}
.footer__content-attribution {
  font-size: 14px;
  color: var(--dark-moderate-cyan);
  text-align: center;
}
.footer__content-attribution > p > a {
  color: var(--dark-blue);
}

.header__wrapper {
  display: flex;
  padding: 1.5rem 0;
  background-color: var(--light-blue);
  height: 70px;
  width: 100%;
}
@media (min-width: 48em) {
  .header__wrapper {
    height: 100%;
  }
}
.header__container {
  display: initial;
}
@media (min-width: 48em) {
  .header__container {
    display: flex;
    justify-content: space-between;
  }
}
.header__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__item-image > a > img {
  width: 100%;
}
@media (min-width: 48em) {
  .header__item-image > a > img {
    width: 200px;
  }
}
.header__item-button {
  cursor: pointer;
}
@media (min-width: 48em) {
  .header__item-button {
    display: none;
  }
}
.header__item-button .bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: var(--white);
}
.header__item-button.-active > .bar:nth-child(2) {
  opacity: 0;
}
.header__item-button.-active > .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
.header__item-button.-active > .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}
.header__nav-menu {
  background-color: var(--white);
  position: relative;
  display: flex;
  text-align: center;
  justify-content: center;
  margin: 2rem 0 1rem 0;
  padding: 2rem 0;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  position: relative;
}
@media (min-width: 48em) {
  .header__nav-menu {
    display: flex;
    margin: 0;
    padding: 0;
    background-color: transparent;
    opacity: 1;
  }
}
.header__nav-menu::before {
  content: "";
  position: absolute;
  top: -20px;
  right: 0;
  background-color: var(--white);
  border-top: 10px solid var(--light-blue);
  border-right: 10px solid var(--white);
  border-left: 10px solid var(--light-blue);
  border-bottom: 10px solid var(--white);
}
@media (min-width: 48em) {
  .header__nav-menu::before {
    border: none;
  }
}
.header__nav-menu.is_active {
  opacity: 1;
  z-index: 1;
}
.header__nav-unordered {
  display: block;
  line-height: 3.5;
}
@media (min-width: 48em) {
  .header__nav-unordered {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 480px;
  }
}
.header__nav-list > .header__nav-anchor {
  color: var(--dark-grayish-blue);
}
@media (min-width: 48em) {
  .header__nav-list > .header__nav-anchor {
    color: var(--white);
  }
}
.header__nav-list > .header__nav-anchor:hover {
  color: var(--yellow);
  transition: all 0.3s ease-in-out;
}
.header__nav-list:last-child {
  font-family: var(--font-heading);
  padding: 0 1.85rem;
  background-color: var(--yellow);
  margin-top: 1rem;
  border-radius: 50px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
@media (min-width: 48em) {
  .header__nav-list:last-child {
    margin-top: 0;
    background-color: var(--white);
  }
}
.header__nav-list:last-child > .header__nav-anchor {
  transition: 0.3s ease-in-out;
  color: var(--dark-blue);
}
.header__nav-list:last-child:hover {
  background-color: var(--light-blue);
}
@media (min-width: 48em) {
  .header__nav-list:last-child:hover {
    background-color: var(--light-blue-hover);
  }
}
.header__nav-list:last-child:hover > .header__nav-anchor:last-child {
  transition: 0.3s ease-in-out;
  color: var(--white);
}

.container {
  width: min(90rem, 100%);
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}
@media (min-width: 48em) {
  .container {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}

.--margin-top {
  margin-top: 2rem;
}
@media (min-width: 48em) {
  .--margin-top {
    margin-top: 3.5;
  }
}

@keyframes indicatorAnimation {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
.home__banner-wrapper {
  background-color: var(--light-blue);
}
.home__banner-container {
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
}
.home__banner-image {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: relative;
}
.home__banner-image > picture > img {
  width: 100%;
  max-height: 270px;
  object-fit: cover;
  object-position: 100% 100%;
  vertical-align: bottom;
}
@media (min-width: 30em) {
  .home__banner-image > picture > img {
    max-height: 400;
    object-position: 85% 85%;
  }
}
@media (min-width: 48em) {
  .home__banner-image > picture > img {
    max-height: 420px;
  }
}
@media (min-width: 80em) {
  .home__banner-image > picture > img {
    object-position: 75% 75%;
  }
}
.home__banner-image.--indicator {
  position: absolute;
  top: -50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: indicatorAnimation 1.3s linear infinite;
}
@media (min-width: 48em) {
  .home__banner-image.--indicator {
    top: -80px;
  }
}
.home__banner-title {
  width: 100%;
  text-align: center;
}
.home__banner-title > h1 {
  color: var(--white);
}

.home__about-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}
.home__about-content .learn-btn::after {
  background-color: var(--yellow);
}
.home__about-content:nth-child(2n) {
  flex-direction: row;
}
.home__about-content:nth-child(2n) .learn-btn::after {
  background-color: var(--soft-red);
}
.home__about-image {
  display: inline-block;
  width: 100%;
  height: 100%;
}
@media (min-width: 48em) {
  .home__about-image {
    width: 50%;
  }
}
.home__about-image > picture > img {
  vertical-align: bottom;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
@media (min-width: 48em) {
  .home__about-image > picture > img {
    height: 500px;
  }
}
@media (min-width: 80em) {
  .home__about-image > picture > img {
    height: 600px;
  }
}
.home__about-title {
  padding: 0 2rem;
  text-align: center;
  margin: 3.5rem 0;
}
@media (min-width: 48em) {
  .home__about-title {
    width: 50%;
    padding: 0 2.5rem;
    text-align: left;
  }
}
@media (min-width: 80em) {
  .home__about-title {
    padding: 0 6rem;
  }
}
.home__about-title > p {
  color: var(--dark-grayish-blue);
  line-height: 1.75;
  margin-bottom: 2rem;
}
@media (min-width: 48em) {
  .home__about-title > p {
    margin-bottom: 3rem;
  }
}

.home__services-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.home__services-content {
  width: 100%;
  position: relative;
}
@media (min-width: 48em) {
  .home__services-content {
    width: 50%;
  }
}
.home__services-text {
  display: flex;
  height: 35%;
  justify-content: center;
  width: 100%;
  position: absolute;
  z-index: 1;
  bottom: 0;
  text-align: center;
}
@media (min-width: 48em) {
  .home__services-text {
    height: 45%;
  }
}
@media (min-width: 80em) {
  .home__services-text {
    height: 35%;
  }
}
.home__services-text-wrapper {
  width: 100%;
  padding: 0 1.5rem;
}
@media (min-width: 80em) {
  .home__services-text-wrapper {
    width: 70%;
  }
}
.home__services-text-wrapper > p {
  line-height: 1.5;
  color: var(--dark-blue);
  font-size: clamp(18px, 3vw, 18px);
}
@media (min-width: 48em) {
  .home__services-text-wrapper > p {
    line-height: 1.2;
  }
}
@media (min-width: 80em) {
  .home__services-text-wrapper > p {
    line-height: 1.5;
  }
}
.home__services-image {
  position: relative;
  width: 100%;
  height: 100%;
}
.home__services-image > picture > img {
  width: 100%;
  height: 100%;
}

.home__testimonial-wrapper {
  margin: 3.5rem 0;
}
@media (min-width: 48em) {
  .home__testimonial-wrapper {
    margin: 10rem 0;
  }
}
.home__testimonial .container {
  max-width: 1200px;
}
.home__testimonial-content {
  width: 100%;
  text-align: center;
}
@media (min-width: 48em) {
  .home__testimonial-content {
    display: flex;
    justify-content: space-between;
  }
}
.home__testimonial-title > h4 {
  text-align: center;
  letter-spacing: 5px;
  color: var(--grayish-blue);
  margin-bottom: 3.5rem;
}
.home__testimonial-item {
  width: 100%;
  line-height: 1.5;
  margin-bottom: 5rem;
}
@media (min-width: 48em) {
  .home__testimonial-item {
    width: 300px;
    margin-bottom: 3rem;
    margin-top: 3rem;
  }
}
.home__testimonial-item > img {
  width: 80px;
  border-radius: 50%;
}
.home__testimonial-item > p {
  color: var(--dark-grayish-blue);
  margin-top: 2rem;
}
.home__testimonial-item > span {
  font-size: clamp(14px, 1vw, 16px);
  color: var(--grayish-blue);
}
.home__testimonial-person {
  line-height: 0.5;
  margin-top: 3rem;
}
@media (min-width: 48em) {
  .home__testimonial-person {
    margin-top: 5rem;
  }
}
.home__testimonial-person > p {
  font-family: var(--font-heading);
  font-size: clamp(18px, 2vw, 22px);
}
.home__testimonial-person > span {
  color: var(--grayish-blue);
}

.home__projects-content {
  display: flex;
  flex-wrap: wrap;
}
.home__projects-item {
  width: 50%;
  overflow: hidden;
}
@media (min-width: 48em) {
  .home__projects-item {
    width: 25%;
  }
}
.home__projects-item:hover {
  transform: scale(1.2);
}
.home__projects-item > picture > img {
  width: 100%;
  height: 100%;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}
.home__projects-item > picture > img:hover {
  transform: scale(1.2);
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}